.addRecipeFormWrapper {
  padding: 1rem;
  background: #fff;
  border-radius: 10px;
  max-width: 500px;
  width: 100%;
  box-sizing: border-box;
}
.inputTitle,
.textDescription{
  width: 100%;
  font-size: 18px;
  padding: 0.5rem;
  border: 1px solid rgb(156, 156, 156);
}
.inputTitle{
  box-shadow: none;
  height: 40px;
  border-radius: 4px;
  margin-bottom: 2rem;
}
.textDescription{
  resize: vertical;
  min-height: 300px;
  border-radius: 4px;
}
.buttonAddRecipe {
  padding: 1rem 2rem;
  border: none;
  border-radius: 1rem;
  font-size: 18px;
  background: #feaf26;
  color: white;
  box-shadow: 0px 0px 47px 2px rgba(34, 60, 80, 0.2);
  cursor: pointer;
  margin: 0 auto;
  display: block;
  margin-top: 1rem;
}
.formValidate{
  margin: 1rem 0;
  text-align: center;
}
